@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px;
}

/* Card section starts here */
.card{
  background: #40475C;
  color: #EBEFF8;
  padding: 25px;
  width: 100%;
  border-radius: 12px;
}

.card h1{
  font-weight: bolder;
  padding-left: 20px;
  padding-top: 10px;
}

/* NFT Cards inside the card conatiner */

.nft-container{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  gap: 20px;
  margin: 20px;
}
.nft-card{
  padding: 10px;
  background: #EBEFF8;
  border-radius: 12px;
  cursor: pointer;
  color: #0D0C0E;
  margin-bottom: 30px;
}

.nft-card img{
    height: 200px;
    width: 250px;
    border-radius: 12px;
}

.inner-nft{
  padding: 10px;
  font-weight: bold;
  line-height: 30px;
  border-bottom: 2px solid #40475C;
}

.inner-nft h2{
  color: #1D3557;
}

.inner-nft p{
  font-size: 14px;
}

.price-and-time{
  display: flex;
  justify-content: space-between;
}

.creator{
  font-size: 12px;
  text-align: center;
  font-weight: bolder;
  padding-top: 5px;
  padding-bottom: 5px;
}

.price,.creator,.time{
  transition: 0.5s ease;
}

.price:hover,.time:hover,.creator:hover{
  color: #40475C;
}

footer{
  text-align: center;
  font-size: 14px;
  font-weight: bolder;
  padding-top: 40px;
}

/* Responsiveness starts here */
/* Tabs */
@media (max-width:991px){
  .nft-container{
    grid-template-columns: repeat(2,1fr);
    gap: 50px;
    margin-top: 50px;
  }
}
/* Mobile */
@media only screen and (max-width: 480px){
  .nft-container{
    grid-template-columns: 1fr;
    margin-top: 20px;
    gap: 20px;
  }
  .card h1{
    text-align: center;
  }
}